<template>
    <div>
        <van-nav-bar title="不凡" left-text="返回" left-arrow @click-left="onClickLeft" />
        <br>
        <van-search v-model="mapVal" placeholder="请输入搜索的地点" />
        <ul>
            <li class="keywordItem" v-for="item in mapKeyword" :key="item.id" @click="gohome(item)">{{item.name}}</li>
        </ul>
        <div id="container"></div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                mapVal: '',
                city: '',
                mapKeyword: [],
                mapdidian: {},
            };
        },
        watch: {
            mapVal() {
                var that = this;
                AMap.plugin('AMap.Autocomplete', function () {
                    // 实例化Autocomplete
                    var autoOptions = {
                        //city 限定城市，默认全国
                        city: that.city,
                    }
                    var autoComplete = new AMap.Autocomplete(autoOptions);
                    autoComplete.search(that.mapVal, function (status, result) {
                        // 搜索成功时，result即是对应的匹配数据
                        that.mapKeyword = result.tips;
                    })
                })
            }
        },
        computed: {

        },
        created() {

        },
        mounted() {
            var that = this;
            AMap.plugin('AMap.CitySearch', function () {
                var citySearch = new AMap.CitySearch()
                citySearch.getLocalCity(function (status, result) {
                    if (status === 'complete' && result.info === 'OK') {
                        // 查询成功，result即为当前所在城市信息
                        that.city = result.city;
                    }
                })
            });
            var lng = this.$store.state.mapLocation.location.lng;
            var lat = this.$store.state.mapLocation.location.lat;
            var map = new AMap.Map('container', {
                zoom: 15, //级别
                center: [lng, lat],
                // ], //中心点坐标
            });
            var marker = new AMap.Marker({
                position: new AMap.LngLat(lng, lat), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]

            });
            map.add(marker);
        },
        methods: {
            onClickLeft() {
                // console.log(222);
                this.$router.push("/home");
            },
            gohome(item) {
                this.$store.commit("upt", item);
                this.mapKeyword = [];
                this.mapVal = '';
                this.mapdidian = this.$store.state.mapLocation;
                var map = new AMap.Map('container', {
                    zoom: 18, //级别
                    center: [this.mapdidian.location.lng, this.mapdidian.location.lat], //中心点坐标
                });
                var marker = new AMap.Marker({
                    position: new AMap.LngLat(this.mapdidian.location.lng, this.mapdidian.location
                    .lat), // 经纬度对象，也可以是经纬度构成的一维数组[116.39, 39.9]

                });
                map.add(marker);
            }
        },
    };
</script>

<style scoped lang="scss">
    #container {
        position: absolute;
        top: 550px;
        width: 100%;
        height: 200px;
    }

    .keywordItem {
        padding: 10px;
        background-color: #fff;
    }
</style>